<template>
  <div class="sun-modal">
    <el-dialog v-model="show" :title="data.title" :width="data.width" center>
      <!-- <span>It should be noted that the content will not be aligned in center by default</span> -->
      <slot name="content"></slot>
      <template #footer>
        <div class="dialog-footer">
          <el-button @click="show = false">{{ data.cancelText }}</el-button>
          <el-button type="primary" @click="() => emits('submit')">{{ data.confirmText }}</el-button>
        </div>
      </template>
    </el-dialog>
  </div>
</template>

<script setup>
import { computed } from "vue"

const props = defineProps({
  show: {
    type: Boolean,
    default: false
  },
  data: {
    type: Object,
    default: () => {}
  }
})

const emits = defineEmits(["update:show", "submit"])
const show = computed({
  get: () => props.show,
  set: val => {
    emits("update:show", val)
  }
})
const height = computed(() => props.data.height)

const onConfirm = () => {
  emits("submit")
}
</script>

<style lang="scss" scoped>
.sun-modal {
  //弹出层里内容的高度
  :deep(.el-dialog__body) {
    // height: 300px !important;
    height: v-bind(height) !important;
    overflow: hidden;
    overflow-y: scroll;
  }
  .dialog-footer {
    display: flex;
    justify-content: center;
    gap: 32px;
  }
}
</style>
