<template>
  <el-table :data="data" v-bind="$attrs" @selection-change="handleSelectionChange">
    <el-table-column v-for="(item, index) in column" :key="index" :formatter="item.formatter" :type="item.type" :prop="item.prop" :label="item.label" :width="item.width" :minWidth="item.minWidth" :fixed="item.fixed" :showOverflowTooltip="item.toolTip">
      <template #default="{ row }">
        <slot :name="item.slot" :row="row" v-if="item.slot"></slot>
        <component :is="item.render" :row="row" :index="index" v-if="item.render" />
      </template>
    </el-table-column>
  </el-table>
  <el-pagination v-if="isPage" class="pagination" :page-sizes="[100, 200, 300, 400]" layout="total, sizes, prev, pager, next, jumper" background :current-page="page.pageNum" :page-size="page.pageSize" :small="page.small" :total="page.total" @size-change="handleSizeChange" @current-change="handleCurrentChange" />
</template>

<script setup>
import { ref, defineProps, defineEmits } from 'vue'

const props = defineProps({
  data: {
    type: Array,
    default: () => []
  },
  column: {
    type: Array,
    default: () => []
  },
  // height: {
  //   type: Number,
  //   default: 400
  // },
  // border: {
  //   type: Boolean,
  //   default: false
  // },
  // rowClassName: {
  //   type: String,
  //   default: ''
  // },
  // cellClassName: {
  //   type: String,
  //   default: ''
  // },
  // spanMethod: {
  //   type: Object,
  //   default: () => { }
  // },
  isPage: {
    type: Boolean,
    default: true
  },
  page: {
    type: Object,
    default: {
      pageNum: 1,
      pageSize: 30,
      small: false,
      total: 100
    }
  }
})

const emits = defineEmits(['selection-change', 'size-change', 'current-change'])


// 当选择项发生变化时会触发该事件
const handleSelectionChange = (data) => {
  emits('selection-change', data)
}

// page-size 改变时触发
const handleSizeChange = (size) => {
  emits('size-change', size)
}

// current-page 改变时触发
const handleCurrentChange = (num) => {
  emits('current-change', num)
}

</script>

<style scoped lang="scss">
.pagination {
  margin: 10px 0;
  float: right;
}

:deep(.table-border) {
  border-right: 1px solid #ebeef5 !important;
}

:deep(.table-background) {
  background: orange !important;
}
</style>
